import React from 'react';
import Page from './Page';
import p5 from 'p5';
import { VisualParticle } from '../libs/particle';
import { Vector } from '../libs/vector';
const P_NUM = 1000;

const blocks = [
  {
    name: 'Random Particles',
    renderFunction: (p: p5) => {
      const particles = [];
      for (let i = 0; i < P_NUM; i++) {
        const x = p.random(p.width);
        const y = p.random(p.height);
        particles[i] = new VisualParticle(x, y);
      }
      for (let time = 0; time < 100; time++) {
        particles.forEach((particle) => {
          const x = p.random(-1, 1);
          const y = p.random(-1, 1);
          const force = new Vector(x, y);
          particle.apply(force);
          particle.update();
          particle.display(p);
        });
      }
    },
  },
];

export default () => <Page blocks={blocks} />;
